.footer {
  position: sticky;
  z-index: -1;
  bottom: 0;
  left: 0;
  right: 0;

  height: calc(100vw * (555 / 1260));
  min-height: 200px;
  max-height: 555px;

  background-image: url(../images/footer-bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
